//引入网络请求
import $H from '@/common/request.js';
import $C from '@/common/config.js';
import $U from '@/common/util.js';
import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
import divider from '@/components/divider.vue';
export default {
	components:{
		divider,
		uniLoadMore
	},
	data() {
		return {
			background:{
				backgroundImage: 'linear-gradient(0deg, #FF7200 0%, #FFB183 100%)'
			},
			tabIndex:0,
			time:"2020.01.20",
			orderNum:"",
			orderPrice:"",
			yuE:"",
			type:1,
			listData:[],
			catagor: [ {
					name: '优惠买单',
					id:2
				},
				{
					name: '商城订单',
					id:3
				}
			],
			page:1,
			reload: true,
			loadMore: false,
			status: 'more',
			nodata:false,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '没有更多'
			}
		}
	},
	
	onLoad() {
		this.getHeader()
		this.getList()
	},
	
	
	onReachBottom() {
			this.loadMore = true;
			this.page++;  
			if (this.status == 'noMore') {
				return;
			}
			this.status = 'loading';
		    this.getList()
	},
	
	
	methods: {
		
		getHeader(){
			// 发送到服务端
			$H.get("/order/profit").then((res) => {
				//请求成功
				console.log(res)
				this.orderNum=res.data.orderListComplete
				this.orderPrice=res.data.orderSumPayPrice
				this.yuE=res.data.account
				
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		getList(){
			
			let data={
				type:this.type,
				size:10,
				current:this.page
			}
			// 发送到服务端
			$H.get("/order/page/all",data).then((res) => {
				//请求成功
				
				let list = res.data.records;
				let pages=res.data.pages;
				 
				 console.log(list)
				 
				if(pages>1&&this.page<=pages){
					this.listData = this.reload ? list : this.listData.concat(list);
					this.nodata=false;
				}
				else if(pages==0){
					this.listData=[];
					this.nodata=true;
					this.status ='noMore'
				}
				else if(pages==1){
					this.listData = list
					this.status ='noMore'
					this.nodata=false;
				}
				else{
					this.status ='noMore'
				}
				
				console.log(this.listData)
				
				this.reload = false;
				
			}).catch((e) => {
				
				//请求失败
				console.log("失败"+e)
			})
		},
		
		
		
		changeIndex(index) {
			if (this.tabIndex == index) {
				return;
			}
			this.tabIndex = index;
			this.reload=true
			if(index==0){
				this.type=1;
				this.page=1
				this.reload=true
				this.status = 'more';
				this.getList()
			}
			else{
				this.type=2;
				this.page=1
				this.reload=true
				this.status = 'more';
				this.getList()
			}
		},
		
		toSearch(){
			uni.navigateTo({
				url:'../../dingdan/ziDingYiChaXun/ziDingYiChaXun'
			})
		},
		
		toOrder(){
			uni.navigateTo({
				url:'../../yinbao/orderAnalyze/orderAnalyze'
			})
		},
		
		toZiJin(){
			uni.navigateTo({
				url:'../../yinbao/yuE/yuE'
			})
		}
	}
}
